.side-panel__appear_12uk0,
.side-panel__enter_12uk0 {
    opacity: 0;
    transform: translateY(15px);
}

.side-panel__appearActive_12uk0,
.side-panel__enterActive_12uk0 {
    opacity: 1;
    transform: translateY(0);
    transition: opacity 200ms ease-in, transform 200ms ease-in;
}

.side-panel__exit_12uk0 {
    opacity: 1;
    transform: translateY(0);
}

.side-panel__exitActive_12uk0,
.side-panel__exitDone_12uk0 {
    opacity: 0;
    transform: translateY(15px);
    transition: opacity 200ms ease-out, transform 200ms ease-out;
}
