:root {
    --color-light-border-link: #007aff;
    --color-light-border-primary: #dbdee1;
    --color-light-graphic-positive: #2fc26e;
    --color-light-graphic-primary: #0b1f35;
    --color-light-graphic-primary-inverted: #fff;
    --color-light-graphic-quaternary: #dbdee1;
    --color-light-text-primary: #0b1f35;
    --color-light-bg-primary-inverted-alpha-7: rgba(11, 31, 53, 0.07);
    --color-light-bg-primary-inverted-alpha-15: rgba(11, 31, 53, 0.15);
}
:root {

    /* Hard */

    /* Up */

    /* Hard up */
}
:root {
    --gap-3xs: 2px;
    --gap-2xs: 4px;
    --gap-xs: 8px;
}
:root {
    --focus-color: var(--color-light-border-link);
}
:root {
    --steps-option-svg-color: var(--color-light-graphic-positive);
    --steps-dash-border: 2px solid var(--color-light-graphic-positive);
}
.steps__step_wrcl3 {
    display: flex;
    outline: none
}
.steps__step_wrcl3:not(.steps__vertical_wrcl3) {
        align-items: center;
    }
.steps__step_wrcl3.steps__interactive_wrcl3:not(.steps__disabled_wrcl3):hover {
    cursor: pointer;
}
.steps__step_wrcl3.steps__disabled_wrcl3:hover .steps__text_wrcl3 {
        background: unset;
    }
.steps__step_wrcl3.steps__disabled_wrcl3 .steps__indicator_wrcl3 {
        cursor: unset;
    }
.steps__indicator_wrcl3 {
    cursor: pointer;
    display: flex;
    align-items: center;
    margin-right: var(--gap-2xs)
}
.steps__indicator_wrcl3.steps__vertical_wrcl3 {
        flex-direction: column;
        margin-right: var(--gap-xs);
    }
.steps__option_wrcl3 {
    font-size: 14px;
    line-height: 20px;
    font-weight: 700;

    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color-light-graphic-primary);
    background: var(--color-light-graphic-quaternary);
    min-width: 24px;
    height: 24px;
    border-radius: 50%
}
.steps__option_wrcl3.steps__vertical_wrcl3 {
        margin-top: var(--gap-xs);
    }
.steps__dash_wrcl3 {
    flex: 1 1 auto;
    min-width: 24px;
    border-top: 2px solid var(--color-light-border-primary);
    margin-right: var(--gap-xs)
}
.steps__dash_wrcl3.steps__completed_wrcl3 {
        border-top: var(--steps-dash-border);
    }
.steps__dash_wrcl3.steps__vertical_wrcl3 {
        min-width: unset;
        min-height: 24px;
        margin-right: 0;
        margin-top: var(--gap-xs);
        border-left: 2px solid var(--color-light-border-primary);
        border-top: none;
    }
.steps__dash_wrcl3.steps__completed_wrcl3.steps__vertical_wrcl3 {
        border-left: var(--steps-dash-border);
    }
.steps__text_wrcl3 {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;

    transition: background 0.2s;
    padding: var(--gap-xs);
    color: var(--color-light-text-primary);
    border-radius: var(--gap-xs);
    height: min-content
}
.steps__text_wrcl3.steps__interactive_wrcl3:not(.steps__disabled_wrcl3):hover {
            background: var(--color-light-bg-primary-inverted-alpha-7);
        }
.steps__text_wrcl3.steps__interactive_wrcl3:not(.steps__disabled_wrcl3):active {
            background: var(--color-light-bg-primary-inverted-alpha-15);
        }
.steps__focused_wrcl3 {
    outline: var(--gap-3xs) solid var(--focus-color);
    outline-offset: var(--gap-3xs);
}
.steps__selected_wrcl3 .steps__text_wrcl3 {
        color: var(--color-light-graphic-primary);
    }
.steps__selected_wrcl3 .steps__option_wrcl3 {
        color: var(--color-light-graphic-primary-inverted);
        background: var(--color-light-graphic-primary);
    }
.steps__checkbox_wrcl3 {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    border: 2px solid var(--color-light-graphic-quaternary);
}
.steps__dot_wrcl3 {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: var(--color-light-graphic-quaternary);
}
.steps__selected_wrcl3 .steps__checkbox_wrcl3 {
    border: 2px solid var(--color-light-graphic-primary)
}
.steps__selected_wrcl3 .steps__checkbox_wrcl3 .steps__dot_wrcl3 {
        background: var(--color-light-graphic-primary);
    }
.steps__option_wrcl3:not(.steps__error_wrcl3).steps__unordered_wrcl3 {
    background: unset;
}
.steps__completedIndicator_wrcl3 > [class*='positive'] {
    color: var(--steps-option-svg-color);
    background-color: var(--steps-option-svg-color);
}
